<template>
    <div
        class="btn"
        @click="onClick"
    >
        <img class="btn-image" src="@/assets/images/button/i_next.png"/>
    </div>
</template>

<script>
export default {
    name: 'NextBtn',
    props: {
    },
    computed: {
        btnStyle() {
            let color = ''
            if (this.state === 'start' || this.state === 'recognizing') {
                color = 'back-white'
            } else {
                color = 'back-blue'
            }

            if (this.dimBtn) {
                color = 'cover'
            } 
            return color
        }
    },
    methods: {
        onClick() {
            this.$emit("onClick")
        },
        
    }
}
</script>

<style lang="scss" scoped>
.btn{
    position: fixed;
    bottom: 26px;
    right: 20px;
    &-image {
        width: 40px;
        height: 40px;
    }
}
.back{
    &-white {
        width: 100%;
        height: 100%;
        background: #ffffff !important;
        color: #4e63da;
        border: 1px solid #4e63da;
    }
    &-blue {
        width: 100%;
        height: 100%;
        background: #4e63da !important;
        color: #ffffff;
    }
}

.cover {
    background: #b8c0f0 !important;
    color: #ffffff;
}
.cover_dim {
    background: #b8c0f0;
    color: #ffffff;
}
</style>