<template>
    <div v-if="!viewHidden">
        <div v-if="loading" class="audio-loading-wrap">
            <loading-animation />
            <p class="color_black font_medium">   ...</p>
        </div>
        <div v-else-if="!isRecording" @click="startRecording" class="audio-wrap">
            <img class="audio-icon" src="@/assets/images/icon/btn-record-ready-n.png"/>
        </div>
        <div v-else @click="stopRecording" class="audio-wrap">
            <img class="audio-icon" src="@/assets/images/icon/btn-record-stop-n.png"/>
        </div>
        <audio ref="audioPlayer" controls class="audio"></audio>
    </div>
    <div v-else>
        <div v-if="voiceState === 'replay'" class="audio-replay">
            <div class="audio-replay-text">
                  
            </div>
            <img class="audio-replay-icon" src="@/assets/images/icon/i_down_arrow_24.png"/>
        </div>
        <div v-else class="audio-mid-wrap">
            <img class="audio-mid-gradation" src="@/assets/images/voice/i_gradation_bottom.png"/>
            <div class="audio-mid-text">
                {{ getMidResult }}
            </div>
        </div>
    </div>
</template>

<script>
import apiCaller from '@/utils/apiCaller'
import LoadingAnimation from '../lottie/LoadingAnimation.vue';

export default {
    components: { LoadingAnimation },
    data() {
        return {
            viewHidden: true,
            ticketId: null,
            loading: false,
            isRecording: false,
            audioContext: null,
            mediaStream: null,
            audioInput: null,
            scriptProcessorNode: null,
            midResult: '',
            // midResult: '\"  .\"',
            voiceState: null,
        };
    },
    computed: {
        getMidResult() {
            const truncatedString = this.midResult.slice(-111)
            return truncatedString
            
            // return '      ,                ,                ,                ,          '
        }
    },
    methods: {
        async startRecording() {
            this.isRecording = true
            this.midResult = ''
            await this.selvyPrepareSTT()

            try {
                this.mediaStream = await navigator.mediaDevices.getUserMedia({ audio: true })
                this.audioContext = new (window.AudioContext || window.webkitAudioContext)({sampleRate: 16000})
                this.audioInput = this.audioContext.createMediaStreamSource(this.mediaStream)

                this.scriptProcessorNode = this.audioContext.createScriptProcessor(4096, 1, 1)

                this.scriptProcessorNode.onaudioprocess = event => {
                    const inputData = event.inputBuffer.getChannelData(0)
                    const pcm16Array = this.float32ArrayToPcm16(inputData)
                    const base64Data = this.arrayBufferToBase64(pcm16Array.buffer)

                    this.selvySendaudioSTT(base64Data)
                }

                this.audioInput.connect(this.scriptProcessorNode)
                this.scriptProcessorNode.connect(this.audioContext.destination)
            } catch (error) {
                console.log("[ryun]: Error accessing microphone: ", error)
            }
        },
        async stopRecording() {
            if (this.audioContext) {
                this.audioInput.disconnect()
                this.scriptProcessorNode.disconnect()
                this.audioContext.close()
                this.audioContext = null

                this.isRecording = false

                this.selvyFinishSTT()
            }
        },
        async selvySendaudioSTT(audioContent) {
            const params = {
                ticketId: this.ticketId,
                audio: {
                    content: audioContent
                },
                lastContent: "F02"
            }

            const res = await apiCaller.selvy.sendaudioSTT(params)
            if (res && res.data.resultCode === '0000') {
                if (res.data.analysisResult) {
                    if (res.data.analysisResult.progressCode === 'P03'|| res.data.analysisResult.progressCode === 'P04' || res.data.analysisResult.progressCode === 'P05') {
                        this.stopRecording()
                    }

                    if (res.data.analysisResult.midresult) {
                        const midresult = res.data.analysisResult.midresult
                        this.midResult = midresult
                        this.voiceState = 'recognizing'
                        this.$emit('midResultState', this.voiceState)
                    }
                }
            } else {
                console.error('[error]', res)
                this.stopRecording()
            }
        },
        async selvyPrepareSTT() {
            const params = {
                engine: {
                    modelId: 0,
                    codec: 1,
                    scoreUse: 1
                },
                sttServer: {
                    authCode: "",
                    ticketIdPrefix: "tbm"
                },
                sttResult: {
                    wordInfo: 1,
                    midresult: "M01"
                },
                engineEpd: {
                    epdUse: 1,
                    startTimeout: 10, // spd timeout(second)
                    durationTimeout: 300 //  max  (second)
                }
            }

            const res = await apiCaller.selvy.prepareSTT(params)
            if (res.data.resultCode === '0000') {
                this.ticketId = res.data.ticketId
                this.voiceState = 'start'
                this.$emit('midResultState', this.voiceState)
            } else {
                console.error('[error]', res)
            }
        },
        async selvyFinishSTT() {
            const params = { ticketId : this.ticketId }
            const res = await apiCaller.selvy.finishSTT(params)
            if (res && res.data.resultCode === '0000') {
                let analysisResult = res.data.analysisResult.result
                // if (analysisResult.indexOf("$") !== -1 | analysisResult === "") {
                //     analysisResult = '\" .   .\"'
                // }
                // this.midResult = analysisResult
                if (analysisResult.indexOf("$") !== -1 | analysisResult === "") {
                    this.voiceState = 'replay'
                } else {
                    this.voiceState = 'finish'
                }
                this.$emit('checkAnalysisResult', analysisResult)
                this.$emit('midResultState', this.voiceState)
                this.loading = false
            } else {
                console.error('[error]', res)
            }   
        },
        float32ArrayToPcm16(float32Array) {
            const pcm16Array = new Int16Array(float32Array.length)

            for (let i = 0; i< float32Array.length; i++) {
                const val = Math.max(-1, Math.min(1, float32Array[i]))
                pcm16Array[i] = val < 0 ? val * 0x8000 : val * 0x7FFF
            }

            return pcm16Array
        },
        concatenateBuffers(buffers) {
            const totalLength = buffers.reduce((acc, buffer) => acc + buffer.length, 0)
            const result = new Int16Array(totalLength)
            let offset = 0
            buffers.forEach(buffer => {
                result.set(buffer, offset)
                offset += buffer.length;
            });
            return result
        },
        arrayBufferToBase64(buffer) {
            const binary = String.fromCharCode.apply(null, new Uint8Array(buffer))
            return btoa(binary)
        },
        changeState(state) {
            this.voiceState = state
        }
    },
    async mounted() {
        this.startRecording()
    },
    async beforeDestroy() {
        this.stopRecording()
    }
};
</script>

<style scoped lang="scss">
.audio {
    display: none;
    &-wrap {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 48px;
        height: 48px;
    }
    &-icon {
        width: 24px;
        height: 24px;
    }
    &-loading-wrap {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 48px;
    }
    &-mid {
        &-wrap {
            height: 100px;
            margin: 0 20px 12px;
            position: relative;
            overflow: hidden;
        }
        &-text {
            position: absolute;
            top: 0;
            font-family: Pretendard;
            font-size: 13px;
            font-weight: 500;
            color: #4e63da;
            height: 100px;
            display: flex;
            align-items: center;
        }
        &-gradation {
            z-index: 10;
            position: absolute;
            top: 10;
            height: 24px;
        }
    }
    &-replay {
        height: 60px;
        padding-bottom: 36px;
        text-align: center;
        background-color: #fff;
        &-text {
            color: #4e63da;
            font-family: 'Pretendard SemiBold';
            font-size: 16px;
            font-weight: 600;
        }
        &-icon {
            width: 24px;
            height: 24px;
        }
    }
}

</style>