import axios from 'axios'
import { v4 as uuidv4 } from 'uuid'

axios.defaults.withCredentials = false
const baseURL = 'https://vc.selvy.ai:443/'

function makeAxios(responseType = 'json') {
    return axios.create({
        baseURL: baseURL,
        withCredentials: false,
        headers: {
            // 'Connection': 'keep-alive',
            'Content-Type': 'application/json',
        },
        responseType
    })
}


const apiCaller = {
    call: async function (
        method,
        path,
        params,
        query,
        authToken = null,
        refreshToken = null,
        responseType = 'json',
        accept = 'application/json' )
    {
        if (method === undefined || path === undefined) {
            throw Error('The parameters method and path must be required.')
        }

        method = method.toUpperCase()
        const key = uuidv4()
        let url = `${path}`
        if (query) {
            url = `${url}?${Object.keys(query)
                .map((key) => `${key}=${query[key]}`)
                .join('&')}`
        }

        let res = null
        try {
            const onRequest = async () => {
                if (method === 'GET') {
                    res = await makeAxios(responseType, accept, refreshToken).get(url)
                }
                else if (method === 'POST') {
                    res = await makeAxios(responseType, accept, refreshToken).post(url, params)
                    console.log(`[API] ${url} `, params, res)
                }
            }
            await onRequest(authToken)
        } catch (e) {
            console.error(JSON.stringify(e, null, 4))
        }
        return res
    }
}
window.addEventListener('stop-api-caller', async () => {
    alert(' .     .')
}, {
    once: true
})
export default {
    auth: {
        async getToken() {
            return await apiCaller.call('POST', '/selvy/stt/auth', null)
        },
    },
    selvy: {
        //  
        async prepareSTT(params) {
            return await apiCaller.call('POST', 'selvy/stt/prepare', params, null)
        },
        //  
        async sendaudioSTT(params) {
            return await apiCaller.call('POST', 'selvy/stt/sendaudio', params, null)
        },
        //  
        async finishSTT(params) {
            return await apiCaller.call('POST', 'selvy/stt/finish', params, null)
        },
    }
}
