<template>
    <div class="lottie">
        <lottie
            :options="options"
            @animCreated="handleAnimation"
        />
    </div>
</template>

<script>
import Lottie from 'vue-lottie'
import animationData from '@/assets/json/loading.json'

export default {
    name: 'LoadingAnimation',
    components: {
        lottie: Lottie,
    },
    props: {
    },
    data() {
        return {
            options: { 
                animationData: animationData, 
                loop: true, 
                autoplay: true, 
            },
            animationSpeed: 1
        }
    },
    methods: {
        handleAnimation: function (anim) {
            this.anim = anim
        }
    },
    mounted() {
    },
    watch: {
    },
}
</script>
<style scoped lang="scss">
.lottie {
    width: 48px;
    height: 48px;
}
</style>